@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600&display=swap');


/*VARIABLES*/
$max-width: 1170px;
$bg-color: #F6F5F7;

body {
    background: $bg-color;
    font-family: 'Poppins', sans-serif;
}

/*GLOBAL STYLES*/

*,
::after,
::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
}

/*SECTION*/

.section {
    width: 90vh;
    margin: 0 auto;
    max-width: $max-width;
}


/*COLOR GENERATOR*/

.container {
    text-align: center;
    display: flex;
    align-items: center;
    height: 100px;
    padding-left: 2rem;

    h1 {
        text-transform: capitalize;
        background-image: linear-gradient(to right, #4458DC 0%, #854FEE 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin-bottom: 0;
        margin-right: 2rem;
        font-size: 1.8rem;
    }
}

input {
    border: 1.5px solid #adb5bd;
    background-color: transparent;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    border-radius: 5px;
    outline: none;
    color: #000000;
}

.btn {
    background-color: black;
    width: 65px;
    border: 1.5px solid black;
    border-radius: 30px;
    padding: 0.5rem 1rem;
    font-size: 1rem;    
    text-transform: capitalize;
    cursor: pointer;
    outline: none;
    margin-left: 0.3rem;

    &:hover {
        transition: all 0.3s;
        background: #F6F5F7;    
    }
}

.fa-search {
    color: white;
    &:hover {
        color: black;
    }
}

input.error {
    border: 1.5px solid red;
}


.colors {
    min-height: calc(100vh - 100px);
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(96px, 1fr));
}

.color {
    padding: 2rem 2rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-end;
}

.percent-value {
    font-weight: 500;
    font-size: 1rem;
    font-family: sans-serif;
    margin-bottom: 0;
    color: #000000;
}

.color-value {
    text-transform: uppercase;
    font-size: 1.2rem;
    font-family: sans-serif;
    font-weight: 600;
    letter-spacing: 0.6px;
    color: #000000;
    margin-bottom: 15px;
}

.color-light .color-value {
    color: #FFF;
}

.color-light .percent-value {
    color: #FFF;
}

/*RESPONSIVE*/

@media screen and(max-width: 1156px) {
    .colors {
        display: grid;
        grid-template-columns: none;
        grid-template-rows: auto;
    }

    .color {
        padding: 1.5rem 0 1.5rem 2rem;
        display: flex;
        align-items:flex-start;
        flex-direction: column;
    }
}

@media screen and (max-width: 992px) {
    .section {
        width: 95vh;
    }
}

@media screen and (max-width: 768px) {
    .color-value {
        font-size: 1.1rem;
    }
}

@media screen and (max-width: 576px) {
    .container {
        padding-left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        h1 {
            font-size: 1.4rem;
            padding: 0.7rem 0;
        }
    }

    .color-value {
        font-size: 1rem;
    }

    input {
        padding: 0.4rem 0.5rem;
    }

    .btn {
        width: 55px;
        padding: 0.5rem 0.9rem;
    }
}
